.vertical-timeline-element-content {
    color: white;
    background: var(--blur-background-color-dark);
    backdrop-filter: blur(var(--background-blur-radius));
    border: 1px solid white;
    border-radius: 10px;
}

.timeline-element-content {
    text-align: left;
}

.timeline-element-content-split-pane {
    display: flex;
    flex-direction: row;
}

.timeline-element-content-split-pane-right {
    /*flex: 1; !* Each child will take up an equal amount of space *!*/
    margin-left: 20px; /* Spacing inside each div */
    padding-left: 20px;
    border-left: 1px solid white;
}

.timeline-element-content-split-pane-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 0;
}

.timeline-element-content-award-hover-text {
    position: absolute;
    background-color: var(--color-gray);
    border: 1px solid white;
    border-radius: 5px;
    width: 25vw;
    z-index: 101;
    padding: 10px;
    margin-left: 30px;
    white-space: normal;
}

ul {
    list-style-type: none; /* Removes the default list style */
    padding: 0; /* Removes the default padding */
}

li::before {
    content: ''; /* Custom bullet */
    color: white; /* Bullet color */
    font-weight: normal;
    display: inline-block; /* Needed to add space between bullet and text */
    width: 0; /* Space between bullet and text */
    margin-left: 0; /* Align bullets with the text */
    margin-top: 15px;
    margin-bottom: 15px;
}

.timeline-element-date {
    width: fit-content;
    white-space: nowrap;
    font-size: 1.2rem;
}

@media only screen and (min-width: 1170px) {
    .vertical-timeline--two-columns
        .vertical-timeline-element-content
        .vertical-timeline-element-date {
        position: absolute;
        left: 124%;
        top: 6px;
        width: fit-content;
        white-space: nowrap;
        font-size: 1.3rem;
        color: white;
        opacity: 100%;
    }
}

.info-line-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info-line-heavy {
    font-weight: bolder;
}

.info-line-italic {
    font-weight: lighter;
    font-style: italic;
}
